import React from 'react'
import '../assets/css/apply.css';
import {Toast, Button, WingBlank, InputItem, WhiteSpace, List } from 'antd-mobile';
import axios from 'axios'

class Apply extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            baseurl: apiurl,
            username: ''
        }
    }

    nameChange(value){
        this.setState({
            username: value
        });
    }

    submit(){
        if(!this.state.username){
            Toast.fail('请输入用户名', 2);
            return false;
        }

        if(!this.state.loading) {
            Toast.loading('提交中…', 30);
            this.setState({
                loading: true
            });

            axios({
                method: 'post',
                url: this.state.baseurl + '/maintenance/apply',
                data: {
                    username: this.state.username
                }
            }).then(function (response) {
                if (response.status == 200 && response.data.code == 0) {
                    this.setState({
                        loading: false,
                        username: ''
                    });
                    Toast.success('提交成功', 3);
                }
                else {
                    this.setState({
                        loading: false
                    });
                    Toast.fail(response.data.message, 2);
                }
            }.bind(this)).catch(function (error) {
                this.setState({
                    loading: false
                });
                Toast.fail('未知错误', 2);
            }.bind(this));
        }
    }

    render() {
        return (
            <div className="d-container">
                <List renderHeader={() => '请先申请成为维护人员'}>
                    <div className="scan-container">
                        <InputItem
                            value={this.state.username}
                            onChange={this.nameChange.bind(this)}
                            placeholder={'请输入用户名'}
                        >用户名</InputItem>
                    </div>
                </List>
                <WhiteSpace size="xl" />
                <WingBlank size="lg">
                    <Button className="btn" type="primary" disabled={this.state.loading && !this.state.username} loading={this.state.loading} onClick={this.submit.bind(this)}>提交申请</Button>
                </WingBlank>
                <WhiteSpace size="xl" />
            </div>
        );
    }
}

export default Apply;



